


// класс применяется ко всему тексту в горизонтальном скроле
.scrollGorizont__text{
  z-index: 50;
  width: 50%;
  @media (max-width:1130px){
    margin: 0 0 0 auto;
    width: 60%;
  }
  @media (max-width:800px){
    width: 70%;
  }
  @media (max-width:700px){
    width: 80%;
  }
  @media (max-width:600px){
    width: 95%;
  }
  .scrollGorizont__section_text_title{
    font-family: "Gerhaus Regular";
    color: #fff;
    .scrollGorizont__section_text_title__top{
      display: flex;
      h4{
        font-size: 3vw;
        color: #222;
        @media (max-width:800px){
          font-size: 25px;
        }
      }
      h3{
        font-size: 2vw;
        margin-top: -30px;
        @media (max-width:800px){
          font-size: 18px;
        }
        span{
          text-decoration: underline;
        }
      }
    }
    .scrollGorizont__section_text_title__center{
      display: flex;
      justify-content: flex-end;
      flex-direction: column;
      p{
        padding-left: 50px;
        font-size: 3vw;
        @media (max-width:800px){
          font-size: 25px;
        }
      }
    }
    .scroll__button{
      display: flex;
      justify-content: flex-end;
      margin-right: 50px;
      margin: 10px 10px 0 0;
     
      a{
        display: flex;
        background-color: #fff;
        color: #222;
        align-items: center;
        border: 1px solid #222;
        border-radius: 10px;
        padding: 5px 10px;
        font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
        transition: .3s ease-in-out;
        img{
          width: 30px;
          height: 30px;
          margin-left: 10px;
        }
        &:hover{
          filter: contrast(70%);
        }
      }
    }
    .scrollGorizont__section_text_title__bottom{
      border-top: 1px solid #fff;
      margin-top: 10px;
      padding-top: 20px;
      display: flex;
      justify-content: space-between;
      h5{
        font-size: 1.2vw;
        margin-right: 15px;
        @media (max-width:820px){
          font-size: 12px;
        }
      }
    }    
  }
}
