.about__wrapper{
    width: 100%;
    height: 100%;
    position: relative;
    z-index: 20;
    background-color: #f5f0ec;
    padding-top: 100px;
    .about__container{
    position: relative;
    width: 100vw;
    height: 100vh;
    font-family: "Gerhaus Regular";
    @media (max-width:717px){
        width: 100%;
        height: 100%;
    }
    .about__mask, .about__body{
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        color: #222;
        font-size: 64px;
        line-height: 56px;
        align-items: center;
        cursor: default;
        @media (max-width:895px){
            font-size: 50px;
            padding-top: 0%;
        }
        @media (max-width:717px){
            font-size: 40px;
            text-align: left;
            line-height: 110%;
        }
        @media (max-width:500px){
            font-size: 30px;
        }
        p{
            width: 1200px;
            padding:20px;
            @media (max-width:895px){
                width: 100%;
                padding: 5px;
            }
            
          }
          span{
            color: #2a4eea;
          }
    }
    .about__mask{
        mask-image: url('../../../public/1.svg');
        mask-repeat: no-repeat;
        mask-size: 40px;
        background: #2a4eea;
        position: absolute;
        color: #fff;
        
        @media (max-width:895px){
            display: none;
        }
      }
    }
}