.awards__wrapper{
    position: relative;
    z-index: 20;
    width: 100%;
    height: 100%;
    h6{
        margin: 100px 0;
        font-size: 8vw;
        color: #fff;
        font-family: "BeatriceBold";
        line-height: 0;
        @media (max-width:650px){
            font-size: 50px;
        }
    }
        .awards__container{
            width: 100vw;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            .awards__item{
                width: 600px;
                margin-left: 200px;
                img{
                    width: 100%;
                    height: 100%;
                }
            }
         }
}